<template>
  <div class="content">
    <div class="title">{{ i18n.$t("common").success }}</div>
    <el-button type="success" round @click="jump">{{
      i18n.$t("home").app
    }}</el-button>
  </div>
</template>

<script>
import { getUserAgent } from "../../../util";
import { useI18n } from "../../../i18nPlugin";
export default {
  data() {
    return {
      password: "",
      email: "",
    };
  },
  mounted() {
    (this.password = this.$route.query.password),
      (this.email = this.$route.query.email);
  },
  methods: {
    jump() {
      if (this.$store.state.viewListen) {
        this.email;
        getUserAgent()
          .then((android) => {
            console.log(android);
            try {
              window.location.href ="qbaytek://www.qbaytek.com:80/app/customer/login?password=" +this.password +"&email=" +this.email;
                setTimeout(() => {
                  throw new Error("没有成功拉起androi!");
                }, 1000);
            } catch (err) {
              console.log(err);
            }
          })
          .catch((ios) => {
            console.log(ios);
            try {
              window.location.href = "Qbay://";
              setTimeout(() => {
                throw new Error("没有成功拉起IOS!");
              }, 1000);
            } catch (err) {
              console.log(err);
            }
          });
      }
    },
  },
  setup() {
    const i18n = useI18n();
    return {
      i18n,
    };
  },
};
</script>

<style lang="scss" scoped>
.content {
  background-color: #ffffff;
  width: 300px;
  margin: 70px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  padding: 10px;
  .title {
    font-weight: bold;
    font-size: 30px;
    margin-bottom: 15px;
  }
  .el-button {
    width: 50%;
  }
}
</style>
